<!-- SCSS -->
<style lang="scss">
@import 'src/scss/index.scss';
.right-service {

  .service-container {
    box-sizing: border-box;
    width: 420px;
    height: 470px;
    background: #15161a;
    padding: 20px 20px 0 20px;
  } // ___________search-bar_________
  .search-bar {
    &>* {
      box-sizing: border-box;
      vertical-align: middle;
      height: 35px;
    }
    &>.search-input {
      border: 1px solid #000;
      background: #111214;
      width: 244px;
      outline: none;
      color: #999;
      text-indent: 10px;
    }

    &>.search-btn {
      border-radius: 4px;
      background-color: $active-color-1;
      &:hover {
        background-color: #f76161;
      }
    }
    .ask-question-btn {
      width: 90px;
      color: #ccc;

      background-color: #565864;
      border-radius: 2px;
      margin-left: 10px;
      line-height: 35px;
      text-align: center;
      &:hover {
        background-color: #6a6c77;
      }
    }
  } //___________questions___________
  .questions {
    margin-top: 26px;

    .questions-title {
      display: inline-block;
      width: 380px;
      padding-bottom: 10px;
      font-size: 16px;
      color: #ccc;
      border-bottom: 1px solid #333;
    }
    .questions-list {
      margin-top: 18px;
      .questions-item {
        &:first-child a {
          color: $active-color-1;
          .dot-icon {
            background-position: -3px -825px;
          }
        }
        .questions-item-a {

          &:hover {
            color: $active-color;
            .dot-icon {
              background-position: -3px -814px;
            }
          }
          line-height: 36px;
          color: #ccc;
          .questions-text {
            display: inline-block;
            vertical-align: middle;
          }
        }
      }
    }
  }
}
</style>

<!-- HTML -->
<template lang="pug">
//-   
.right-service
  icon-title(text="")
  .service-container
    .search-bar.horizontal
      input.search-input(placeholder="")
      a.search-btn.search-icon
      a.ask-question-btn 

    .questions
      a.questions-title 
      ul.questions-list
        li.questions-item(v-for="v in questionsData")
          a.questions-item-a
            i.dot-icon
            p.questions-text {{v}}

</template>

<!-- JS -->
<script>
import iconTitle from '../icon-title.vue'

export default {
  name: 'right-service',
  components: {
    iconTitle
  },
  data() {
    return {
      msg: 'this is from right-service.vue',
      questionsData: [
        'DOTA2',
        '',
        'DOTA2',
        '',
        'DOTA2, ',
        'DOTA2',
        '',
        'XP',
        'ID',
      ]
    }
  },
  methods: {

  }
}
</script>



