<!-- SCSS -->
<style lang="scss">
.carousel-3d {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  background: #15161a;
  .dots {
    position: absolute;
    bottom: 12px;
    left: 50%;
    z-index: 999;

    .dot {
      $width: 12px; // 
      cursor: pointer;
      box-sizing: border-box;
      border: 2px solid transparent;
      border-radius: 50%;

      width: $width;
      height: $width;
      background: #333;
      margin: $width/2;
      float: left;
      &.active-dot {
        border: 2px solid #f84e4e;
      }
    }
  }


  .list-container {

    .item-container {
      // 
      display: inline-block;
      position: absolute;
      transition: left 1s, transform 1s;

      &:hover a.img-title {
        color: #00c0ff;
      }
    }

    .item-img {
      display: block;
    }
    a.img-title {
      // .item-container
      width: 100%;
      background: #222328;

      text-align: center;
      font-size: 16px;
      line-height: 36px;


      position: absolute;
      z-index: 100;
      top: 100%;
      left: 0;
    }
  }
}
</style>

<!-- HTML -->
<template lang="pug">
.carousel-3d(@mouseover="hoverFn",@mouseout="hoverFn")
  ul.dots(:style="{marginLeft:-len*12 +'px'}")
    li.dot(v-for="n in len",:class="{'active-dot': activeIndex===(n-1)}",@click="dotClickFn(n-1)")

  .list-container(:style="{height: height+'px',width:width+'px'}"  )
    a.item-container(
        v-for="(v,i) in imgsArr",
        :class="['img'+i,{med:i===activeIndex, left:i===prevIndex, right:i===nextIndex}]",
        :style="{ 'z-index':getZindex(i),'transform':`scale(${transformFn(i).scale})`,left:`${transformFn(i).left}px`}",
        @click="clickSwitchFn(i)"
      )
      img.item-img(:src="v.src")
      a.img-title(v-if="v.title") {{v.title}}

</template>

<!-- JS -->
<script>
//import XXX from './components/XXX'

export default {
  name: 'vue-carousel-3d',
  props: {
    imgsArr: { //   [{src="",title=""},{...}]
      type: Array,
      required: true
    },
    imgWidth: { // 
      type: Number,
      default: NaN
    },
    width: { // 
      type: Number,
      default: 400
    },
    height: { // 
      type: Number,
      default: 600
    },
    interval: { // 
      type: Number,
      default: 3000
    }
  },
  data() {
    return {
      msg: 'this is from vue-carousel-3d.vue',
      activeIndex: 0,
      timer: null, // 
      timer1: null, // 
      dir: 'right',
      transitionInterval: 600 // 1000
    }
  },
  computed: {
    len() {
      return this.imgsArr.length
    },
    prevIndex() {
      var curr = this.activeIndex - 1
      return curr >= 0 ? curr : this.len - 1
    },
    nextIndex() {
      var curr = this.activeIndex + 1
      return curr === this.len ? 0 : curr
    },

  },
  methods: {
    clickSwitchFn(i) { // 
      if (this.isTransitioning) return // transition
      this.activeIndex = i
    },
    dotClickFn(target) { // 
      if (this.isTransitioning) return // transition

      if (this.timer1) clearInterval(this.timer1)
      if (this.activeIndex === target) return


      var old = this.activeIndex // 
      var abs = Math.abs(target - old)
      if (abs === 1 || abs === (this.len - 1)) { // 1this.len - 1 
        this.activeIndex = target
        return
      }

      // 
      //  1/
      var stepInterval = parseInt(this.transitionInterval / abs)

      var aImg = this.$el.getElementsByClassName('item-container')
      for (var i = 0; i < aImg.length; i++) {
        aImg[i].style.transition = "all " + (stepInterval / 1000).toFixed(2) + 's'
      }

      // 
      target > old ? this.activeIndex++ : this.activeIndex--

      this.timer1 = setInterval(() => {
        // 
        target > old ? this.activeIndex++ : this.activeIndex--

        if (this.activeIndex === target) { // 
          clearInterval(this.timer1)
          setTimeout(() => {
            for (var i = 0; i < aImg.length; i++) {
              aImg[i].style.transition = "all " + this.transitionInterval / 1000 + 's'// 
            }
          }, stepInterval) // 1s
        }
      }, stepInterval)

    },
    getZindex(i) {
      // z-index8z-index10
      if (i === this.activeIndex) {
        return 3
      }

      if (i === this.nextIndex) {
        return this.dir === 'right' ? 1 : 2 //
      }
      if (i === this.prevIndex) {
        return this.dir === 'right' ? 2 : 1
      }
    },

    transformFn(i) { // ileftscale
      var left = (this.width - this.imgWidth) / 2,
        scale = 0.9
      switch (i) {
        case this.activeIndex:
          scale = 1 // 
          break
        case this.prevIndex:
          left = -(1-scale)/2 * this.imgWidth // 0.9 left
          break
        case this.nextIndex:
          left = this.width - this.imgWidth * (scale+(1-scale)/2) // left
          break
        default:
          scale = 0.8
      }
      return { left, scale }
    },

    startInterval() { // 
      this.timer = setInterval(() => {
        this.activeIndex = this.nextIndex
      }, this.interval)
    },
    hoverFn(e) { // 
      if (e.type === 'mouseover') {
        if (this.timer) clearInterval(this.timer)
      } else {
        this.startInterval()
      }
    }
  },
  mounted() {
    this.startInterval()
  },
  watch: {
    activeIndex(newV, oldV, ) {
      // 
      //   -> 
      //  -> 
      this.dir = (newV > oldV && !(newV === this.len - 1 && oldV === 0)) || (newV === 0 && oldV === this.len - 1) ? 'right' : 'left'

      // transition
      this.isTransitioning = true
      setTimeout(() => {
        this.isTransitioning = false // falsetransition
      }, this.transitionInterval)

    }
  }
}
</script>



