<!-- SCSS -->
<style lang="scss">
.layout-fixed-width {
  &>.inner {
    margin: 0 auto;
  }
}
</style>

<!-- HTML -->
<template lang="pug">
.layout-fixed-width
  .inner(:style="{width:width+'px'}")
    slot
</template>

<!-- JS -->
<script>
//import XXX from './components/XXX'

export default {
  name: 'layout-fixed-width',
  props: {
    width: {
      type: Number,
      default: 800
    }
  }
}
</script>



