<!-- SCSS -->
<style lang="scss">
@import 'src/scss/index.scss';
.header-nav {
  .header-nav-item:hover {
    .title {
      color: $active-color;
    }
    background: #000;
    background: url('/static/img/header/data2.home.nav.hover.bg.png') no-repeat top center;
    background-size: cover;
  }
}
</style>

<!-- HTML -->
<template lang="pug">
ul.header-nav.clearfix
  header-nav-item.fl(
      v-for="(v,i) in navData",
      :data="v",
      @mouseover.native="activeIndex=i",
      @mouseout.native="activeIndex=NaN",
      :isDropdown="activeIndex===i"
    )


</template>

<!-- JS -->
<script>
import headerNavItem from './header-nav/header-nav-item.vue'


export default {
  name: 'header-nav',
  components: {
    headerNavItem
  },
  data() {
    return {
      msg: 'this is from header-nav.vue',
      activeIndex: NaN,
      navData: [
        { titleCn: '', titleEn: 'HOME', list: ['', '', '', ''] },
        { titleCn: '', titleEn: 'NEWS', list: ['', '', '', ''] },
        { titleCn: '', titleEn: 'MATCH', list: ['', '', ''] },
        { titleCn: '', titleEn: 'INTERACTION', list: ['', '', '', '', ''] },
        { titleCn: '', titleEn: 'DATA', list: ['', '', '', '', ''] },
        { titleCn: '', titleEn: 'SERVICE', list: ['', '', '', '', '', '', ''] },
        { titleCn: '', titleEn: 'DOWN', list: ['', '', '', ''] },
      ]
    }
  },
  methods: {

  }
}
</script>



